
















import {Vue, Component, Prop, Watch} from 'vue-property-decorator';
@Component
export default class NavLink extends Vue {
  private crumbs: any = [];
  @Watch('$route.name')
  public watchRouteChange() {
    console.log(this.$route);
    const meta = this.$route.meta;
    if (meta) {
      this.crumbs = meta.breadcrumb;
    }
  }
}
